<script>
import { ref, watch, $emit } from 'vue'

export default {
  props: {
    date_p: Array
  },
  setup(props, { emit }) {
    const today = new Date();
    const year = today.getFullYear()
    const month = today.getMonth() + 1
    const date_p = ref([])
    if(props.date_p){
      date_p.value = date_p.value.concat(props.date_p)
    }
    const color = "purple"
    const attrs = ref([
      {
        highlight: {
          color: "purple",
          fillMode: "light"
        },
        dates: date_p
      }
    ])

    //select date style
    const selectAttribute = {
      highlight: {
        style: {
          // Circle styles
          background: "#1F9DDF"
        },
        contentStyle: {
          // Text styles
          color: "white"
        }
      }
    }
    const attributes = []

    const dayclick = (day) => {
      const date = day.id
      
      //是否在今天以前
      let yesterday = new Date(today)
      yesterday.setDate(yesterday.getDate() - 1)
      let clickDay = new Date(date)
      if(clickDay<yesterday)return
      if (date_p.value.includes(day.id)) {
        date_p.value = date_p.value.filter(x => x != day.id)
      } else {
        date_p.value.push(day.id)
      }
    }
    const pageChange = (page) => {
      this.currentPage = page;
      this.fetchData();
    }

    const updateWorkDate = () => {
      emit("updateWorkDate", date_p.value)
      console.log(date_p.value)
    }
    watch(() => date_p.value.length, () => {
      updateWorkDate()
    })

    return { attrs, dayclick, year, month }
  }
}
</script>

<template>
  <div class="testStyle2">
    <!-- from-page 月份不用減1=>11就是11月 -->
    <v-calendar  :min-date='new Date()' is-expanded :from-page="{ month: month, year: year }" :attributes="attrs" @dayclick="dayclick" />
  </div>
</template>

<style lang="scss" scoped>
.testStyle2 {
  font-size: 40px;
}
</style>