export const errorHandle = (msg) => {
    if (typeof (msg.message) == 'string') {
        return msg.message
    } else {
        return 'other error'
    }
}

export const timeHandle = (timeList) => {
    for (let index = 0; index < 24; index++) {
        timeList.push({ "id": index, "time": `${index}:00` })
        timeList.push({ "id": index + 1, "time": `${index}:30` })
    }
}


//getURL
export const getUrlId = () => {
    const getUrlString = location.href;
    const url = getUrlString.split("/");
    const urlId = url[url.length - 1]

    return { urlId }
}
export const getUrlId2 = () => {
    const getUrlString = location.href;
    const url = getUrlString.split("/");
    const urlId = url[url.length - 2]

    return { urlId }
}


// 處理日期
// {start: '2022-12-01 09:00:00', end: '2022-12-01 10:00:00'} 轉成 =>
// return {newDates:["2022/11","2022/12"],info:{'2022/11':[22,23,24,25],'2022/12':[1,2,4,5]}}
//字串轉換fun
const dateToString = (val) => {
    return [val.getFullYear(), val.getMonth() + 1, val.getDate()]
}
//數字補0
export const addZero = (val) => {
    return val < 10 ? '0' + val : val
}

export const monthHandle = (dates) => {
    let newDates = JSON.parse(JSON.stringify(dates))
    let info = {}
    let time = ""
    newDates.forEach(item => {
        item.start = new Date(item.start.replace(/-/g, '/'))
        item.end = new Date(item.end.replace(/-/g, '/'))
        item.info = dateToString(item.start)
        if (info[`${item.info[0]}/${item.info[1]}`]) {
            info[`${item.info[0]}/${item.info[1]}`].push(item.info[2])
        } else {
            info[`${item.info[0]}/${item.info[1]}`] = [item.info[2]]
        }
    })
    if (newDates[0]?.start.getHours() == newDates[0]?.end.getHours()) {
        time = `${newDates[0]?.start.getHours()}:${addZero(newDates[0]?.start.getMinutes())} ~ ${newDates[0]?.end.getHours()}:${addZero(newDates[0]?.end.getMinutes())}(24小時)`
    } else {
        time = `${newDates[0]?.start.getHours()}:${addZero(newDates[0]?.start.getMinutes())} ~ ${newDates[0]?.end.getHours()}:${addZero(newDates[0]?.end.getMinutes())}`
    }

    return { newDates, info, time }
}
// for狀態3 => info 有isDone屬性
export const monthHandle2 = (dates) => {
    let newDates = JSON.parse(JSON.stringify(dates))
    let info = {}
    let time = ""
    newDates.forEach(item => {
        item.start = new Date(item.start.replace(/-/g, '/'))
        item.end = new Date(item.end.replace(/-/g, '/'))
        item.info = dateToString(item.start)
        if (info[`${item.info[0]}/${item.info[1]}`]) {
            info[`${item.info[0]}/${item.info[1]}`].push([item.info[2], item.isDone])
        } else {
            info[`${item.info[0]}/${item.info[1]}`] = [[item.info[2], item.isDone]]
        }
    })
    if (newDates[0]?.start.getHours() == newDates[0]?.end.getHours()) {
        time = '全天(24小時)'
    } else {
        time = `${newDates[0]?.start.getHours()}:${addZero(newDates[0]?.start.getMinutes())} ~ ${newDates[0]?.end.getHours()}:${addZero(newDates[0]?.end.getMinutes())}`
    }

    return { newDates, info, time }
}

//for 接受日期、確認日期
export const monthHandle3 = (dates) => {
    let x = []
    let monthInfo = []
    let dayInfo = {}
    let dateInfo = []
    if (dates?.length > 0) {
        dates.forEach(item => x.push(item.split('-')))
    }
    x.forEach(item => {
        let title = `${item[0]}/${item[1]}`
        monthInfo.push(title)
        if (dayInfo[title]) {
            dayInfo[title].push(item[2])
        } else {
            dayInfo[title] = [item[2]]
        }
    })
    monthInfo = monthInfo.filter((item, index) => monthInfo.indexOf(item) === index)
    if (monthInfo.length == 1) {
        dateInfo.push(`${monthInfo?.[0]}/${dayInfo?.[monthInfo?.[0]]?.join('、')}`)
    }
    if (monthInfo.length > 1) {
        dateInfo.push(`${monthInfo?.[1]}/${dayInfo?.[monthInfo?.[1]]?.join('、')}`)
    }
    return { monthInfo, dayInfo, dateInfo }
}

//病症list 轉 文字
//diseaseList 所有的表、diseaseData 要output的
export const diseaseDataHandle = (diseaseList, diseaseData) => {
    // let output = {"大腸癌":[{"id":"D010101","name":"腹(脹)瀉"},{"id":"D010102","name":"造口"}],"中風":[{"id":"D030101","name":"單側肢體偏癱/麻木"},{"id":"D030102","name":"吞嚥困難/流口水"}]}
    let output = {}
    diseaseData.forEach(item => {
        let firstNum = parseInt(item.substring(1, 3)) - 1
        let secondNum = parseInt(item.substring(3, 5)) - 1
        let thirdNum = parseInt(item.substring(5, 7)) - 1
        if (Object.keys(output).includes(diseaseList[firstNum].name)) {
            output[diseaseList[firstNum].data[secondNum].name].push(diseaseList[firstNum].data[secondNum].data[thirdNum])
        } else if (Object.keys(output).includes(diseaseList[firstNum].data[secondNum].name)) {
            output[diseaseList[firstNum].data[secondNum].name].push(diseaseList[firstNum].data[secondNum].data[thirdNum])
        } else {
            output[diseaseList[firstNum].data[secondNum].name] = [diseaseList[firstNum].data[secondNum].data[thirdNum]]
        }
    })
    return output
}


